.infoToolTip__container {
  padding: 18px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 430px;
  height: 330px;
  transition: 0.3s ease-in-out;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* pointer-events: none; */
  z-index: 1;
}

@media (max-width: 768px) {
  * {
    transition: 0.3s ease-in-out;
  }
  .infoToolTip__container {
    max-width: 282px;
    max-height: 322px;
  }
}
